@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}



.SingleItem .SingleItem__Image__Container{min-height:200px;background-repeat:no-repeat;background-size:cover;border-radius:20px;display:flex;flex-direction:column;align-items:flex-end}.SingleItem .SingleItem__Image__Container .SingleItem__PopularChoice{background:#FF498B;color:#fff;text-align:center;width:70%;min-height:30px;border-bottom-left-radius:20px;border-top-right-radius:20px}.SingleItem .SingleItem__Bottom__Container{padding:5px 0}.SingleItem .SingleItem__Bottom__Container .SingleItem__Name{color:#162D5C}.SingleItem .SingleItem__Bottom__Container .SingleItem__Location{font-weight:lighter;color:gray}

.SectionTitle{padding:1rem 0}.SectionTitle h3{color:#162D5C}

.House{padding:1rem 0}

.Hotel{padding:1rem 0}

.MostPickItem{padding:10px}.MostPickItem .MainItem{height:420px}.MostPickItem .OtherItem{height:200px}.MostPickItem .MostPickItem__Container{width:100%;background-repeat:no-repeat;background-size:cover;border-radius:20px}.MostPickItem .MostPickItem__Container .MoctPickItem__Overlay{border-radius:20px;width:100%;height:100%;background:rgba(0,0,0,0.3);display:flex;flex-direction:column}.MostPickItem .MostPickItem__Container .MoctPickItem__Overlay .Top__Section{height:50%;display:flex;flex-direction:column;align-items:flex-end}.MostPickItem .MostPickItem__Container .MoctPickItem__Overlay .Top__Section .MoctPickItem__PopularChoice{background:#FF498B;color:#fff;text-align:center;width:70%;min-height:30px;border-bottom-left-radius:20px;border-top-right-radius:20px}.MostPickItem .MostPickItem__Container .MoctPickItem__Overlay .Bottom__Section{height:50%;padding-left:1rem;display:flex;flex-direction:row;align-items:flex-end}.MostPickItem .MostPickItem__Container .MoctPickItem__Overlay .Bottom__Section .MostPickItem__Name{color:#fff}.MostPickItem .MostPickItem__Container .MoctPickItem__Overlay .Bottom__Section .MostPickItem__Location{color:#fff}

.MostPick{padding:1rem 0}

.LeftPanel h1{font-weight:bold;color:#162D5C}.LeftPanel p{margin-top:1rem}.LeftPanel button{margin-top:3rem;background:#3252DF}

.RightPanel{padding:1rem;position:relative}.RightPanel .Pannel__Wrapper{height:350px}.RightPanel .Pannel__Wrapper .layer1{border:1px solid #DED6D6;border-radius:5px;border-top-left-radius:50px;top:2rem;left:50px;width:90%;height:90%;position:absolute;z-index:1}.RightPanel .Pannel__Wrapper .layer1 img{border-radius:5px;border-top-left-radius:50px;width:100%;height:100%}.RightPanel .Pannel__Wrapper .layer2{border-radius:5px;border-top-left-radius:50px;width:90%;height:90%;position:absolute;z-index:2}.RightPanel .Pannel__Wrapper .layer2 img{border-radius:5px;border-top-left-radius:50px;width:100%;height:100%}

.PageIntro{padding:2rem 1rem}

.HappyFamily{padding:1rem 0}.HappyFamily .Pannel__Wrapper{height:450px}.HappyFamily .Pannel__Wrapper .layer1{border-radius:5px;border-bottom-right-radius:50px;top:2rem;left:50px;width:70%;height:90%;position:absolute;z-index:2}.HappyFamily .Pannel__Wrapper .layer1 img{border-radius:5px;border-bottom-right-radius:50px;width:100%;height:100%}.HappyFamily .Pannel__Wrapper .layer2{border:1px solid #DED6D6;border-radius:5px;border-bottom-right-radius:50px;width:70%;height:90%;position:absolute;z-index:1}.HappyFamily .Pannel__Wrapper .layer2 img{border-radius:5px;border-bottom-right-radius:50px;width:100%;height:100%}.HappyFamily p{margin-top:1rem}.HappyFamily button{margin-top:3rem;background:#3252DF}

.Footer{padding:1rem 0;border-top:solid 1px #DED6D6}

.FooterLogo{display:flex;justify-content:space-between;align-items:center;padding:1rem 0}.FooterLogo p{color:gray}.FooterLogo img{width:100px}

.FooterLink{padding:1rem 0}.FooterLink a{display:block}

.NavBar{border-bottom:solid 1px #DED6D6}.NavBar img{width:70px}.NavBar .nav-link{font-weight:bold;font-size:20px;color:#162D5C}

